import React from 'react';
import AboutBannerSection from "../bannerSection/aboutBannerSection";
import SkillSection from "../skillSection/skillSection";
import TeamSection from "../teamSection/teamSection";
import ServiceSection from "../serviceSection/serviceSection";
import ContactInfoSection from "../../contactInfoSecton/contactInfoSection";

function AboutPageLayout(props) {
    return (
        <>
            <AboutBannerSection bannerTitle={'About Our Company'} />
            <SkillSection />
            <TeamSection />
            <ServiceSection />
            <ContactInfoSection />
        </>
    );
}

export default AboutPageLayout;