import React, {useState} from 'react';
import style from './singleProductSection.module.css'
import topImage from '../../../img/singleProduct/single-product-01.jpg'
import bottomImage from '../../../img/singleProduct/single-product-02.jpg'

function SingleProductSection(props) {
    const [quantity, setQuantity] = useState(1)
    return (
        <section>
            <div className={style.container}>
                <div className={style.topImage}>
                    <img src={topImage} alt=""/>
                </div>
                <div className="title">
                    <div className={style.top}>
                        <h3>New Green Jacket</h3>
                        <p>$75.00</p>
                    </div>
                    <div className={style.text}>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod kon tempor incididunt
                            ut labore.</p>
                        <div className={'flex items-center'}>
                            <svg className={'rotate-180'} xmlns="http://www.w3.org/2000/svg" width="20%"
                                 viewBox="0 -960 960 960" fill="#2a2a2a">
                                <path
                                    d="m228-240 92-160q-66 0-113-47t-47-113q0-66 47-113t113-47q66 0 113 47t47 113q0 23-5.5 42.5T458-480L320-240h-92Zm360 0 92-160q-66 0-113-47t-47-113q0-66 47-113t113-47q66 0 113 47t47 113q0 23-5.5 42.5T818-480L680-240h-92ZM320-500q25 0 42.5-17.5T380-560q0-25-17.5-42.5T320-620q-25 0-42.5 17.5T260-560q0 25 17.5 42.5T320-500Zm360 0q25 0 42.5-17.5T740-560q0-25-17.5-42.5T680-620q-25 0-42.5 17.5T620-560q0 25 17.5 42.5T680-500Zm0-60Zm-360 0Z"/>
                            </svg>
                            <span>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiuski smod.</span>
                        </div>

                    </div>
                    <div className={style.quantity}>
                        <h4>No. of Orders</h4>
                        <div className={style.counter}>
                            <button onClick={() => quantity > 1 && setQuantity(quantity - 1)}>-</button>
                            <div>{quantity}</div>
                            <button onClick={() => setQuantity(quantity + 1)}>+</button>
                        </div>
                    </div>
                </div>
                <div className={style.bottomImage}>
                    <img src={bottomImage} alt=""/>
                </div>
                <div className={style.addToCart}>
                    <h4>Total: $210.00</h4>
                    <button>Add To Cart</button>
                </div>
            </div>
        </section>
    );
}

export default SingleProductSection;