import React from 'react';
import style from './contactInfoSection.module.css'
function ContactInfoSection(props) {
    return (
        <section>
            <div className={style.container}>
                <div className={style.field}>
                    <div className={'title'}>
                        <h2>By Subscribing To Our Newsletter You Can Get 30% Off</h2>
                        <p><i>Details to details is what makes Hexashop different from the other themes.</i></p>
                    </div>
                    <form action="">
                        <input placeholder={'Your Name'} type="text"/>
                        <input placeholder={'Your Email Address'} type="email"/>
                        <button>
                            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="#fff">
                                <path d="M120-160v-640l760 320-760 320Zm80-120 474-200-474-200v140l240 60-240 60v140Zm0 0v-400 400Z"/>
                            </svg>
                        </button>
                    </form>
                </div>
                <div className={style.contactInfo}>
                    <div className={style.info}>
                        <h3>Store Location:</h3>
                        <p>Sunny Isles Beach, FL 33160, United States</p>
                    </div>
                    <div className={style.info}>
                        <h3>Work Hours:</h3>
                        <p>07:30 AM - 9:30 PM Daily</p>
                    </div>
                    <div className={style.info}>
                        <h3>Phone:</h3>
                        <p>010-020-0340</p>
                    </div>
                    <div className={style.info}>
                        <h3>Email:</h3>
                        <p>mail@domain.com</p>
                    </div>
                    <div className={style.info}>
                        <h3>Office Location:</h3>
                        <p>North Miami Beach</p>
                    </div>
                    <div className={style.info}>
                        <h3>Social Media:</h3>
                        <p>Facebook, Instagram, Behance, Linkedin</p>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default ContactInfoSection;