import React from 'react';
import style from './contactSection.module.css'
function ContactSection(props) {
    return (
        <section>
            <div className={style.container}>
                <div className={style.map}>
                    <map className={'w-full'} name="">
                        <iframe
                            className={'w-full h-full'}
                            src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d1654.730252508318!2d4.801590800432411!3d45.772924259845524!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sfr!4v1720025295636!5m2!1sen!2sfr"
                               allowFullScreen="" loading="lazy"
                            referrerPolicy="no-referrer-when-downgrade"></iframe>
                    </map>
                </div>
                <div>
                    <div className="title">
                        <h2>Say Hello. Don't Be Shy!</h2>
                        <p><i>Details to details is what makes Hexashop different from the other themes.</i></p>
                    </div>
                    <div className={style.field}>
                        <div>
                            <input placeholder={'Your Name'} type="text"/>
                            <input placeholder={'Your Email Address'} type="email"/>
                        </div>
                        <textarea placeholder={'Your Message'} name="" id="" cols="30" rows="5"></textarea>
                        <button type={'submit'}>
                            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px"
                                 fill="#fff">
                                <path
                                    d="M120-160v-640l760 320-760 320Zm80-120 474-200-474-200v140l240 60-240 60v140Zm0 0v-400 400Z"/>
                            </svg>
                        </button>
                    </div>
                </div>

            </div>
        </section>
    );
}

export default ContactSection;