import React, {useEffect, useRef, useState} from 'react';
import style from './header.module.css'
import logo from '../../img/home/logo.png'
import {NavLink} from "react-router-dom";
function Header(props) {

    useEffect(() => {
        window.addEventListener('scroll', handleScroll, { passive: true });
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);
    const [headerScroll, setHeaderScroll] = useState({
        padding: '20px 100px',
        boxShadow: 'none'
    })
    const [navButtonStatus, setNavButtonStatus] = useState(true)
    const mobileNavRef=useRef(null);
    const navbarRef = useRef(null);
    function handleScroll (){
        if (window.scrollY>10){
            setHeaderScroll({
                padding: '15px 100px',
                boxShadow: '0 0 10px rgba(0, 0, 0, .15)',
                border: 'none'
            })
        }else{
            setHeaderScroll({
                padding: '20px 100px',
                boxShadow: 'none'
            })
        }
    }
    function openNav(e){
        if (navButtonStatus){
            navbarRef.current.children.middleLine.style.cssText+=`
            width: 0;
            visibility: none;`
            navbarRef.current.children.topLine.style.cssText+=`
            transform: rotate(45deg) translateY(12px) translateX(2px);
            width: 32px;`
            navbarRef.current.children.bottomLine.style.cssText+=`
            transform: rotate(-45deg) translateY(-12px) translateX(2px);
            width: 32px;`
            mobileNavRef.current.style.cssText+=`
                    height: 450px;
                    visibility: visible;
            `
            for (let i = 0; i < mobileNavRef.current.children.length; i++) {
                mobileNavRef.current.children[i].style.opacity=1
            }
            setNavButtonStatus(false)
        }
        else {
            closeNav(e)
        }
    }
    function closeNav(e){
        navbarRef.current.children.middleLine.style.cssText+=`
            width: 100%;
            visibility: visible;`
        navbarRef.current.children.topLine.style.cssText+=`
            transform: rotate(0) translateY(0) translateX(0);
            width: 22px;`
        navbarRef.current.children.bottomLine.style.cssText+=`
            transform: rotate(0) translateY(0) translateX(0);
            width: 22px;`
        mobileNavRef.current.style.cssText+=`
                    height: 0;
                    visibility: hidden;
                    `
        for (let i = 0; i < mobileNavRef.current.children.length; i++) {
            mobileNavRef.current.children[i].style.opacity=0
        }
        setNavButtonStatus(true)
    }
    return (
        <header style={headerScroll} >
            <div className={style.headerContainer}>
                <div className={style.logo}>
                    <img src={logo} alt=""/>
                </div>
                <nav>
                    <div onClick={openNav} ref={navbarRef} className={style.menuBar}>
                        <div name={'topLine'}></div>
                        <div name={'middleLine'}></div>
                        <div name={'bottomLine'}></div>
                    </div>
                    <ul ref={mobileNavRef} className={style.mobileNavigation}>
                        <li><NavLink onClick={closeNav} to={'./'}>Home</NavLink></li>
                        <li><a onClick={closeNav} href="/#menSection">Men's</a></li>
                        <li><a onClick={closeNav} href="/#womenSection">Women's</a></li>
                        <li><a onClick={closeNav} href="/#kidSection">Kid's</a></li>
                        <li><NavLink onClick={closeNav} to={'/about'}>About Us</NavLink></li>
                        <li><NavLink onClick={closeNav} to={'/product'}>Products</NavLink></li>
                        <li><NavLink onClick={closeNav} to={'/singleProduct'}>Single Product</NavLink></li>
                        <li><NavLink onClick={closeNav} to={'/contact'}>Contact Us</NavLink></li>
                        <li><a onClick={closeNav} href="/#exploreSection">Explore</a></li>
                    </ul>
                    <ul className={style.navigation}>
                        <li><NavLink to={'/'}>Home</NavLink></li>
                        <li><a  href='/#menSection'>Men's</a></li>
                        <li><a href="/#womenSection">Women's</a></li>
                        <li><a href="/#kidSection">Kid's</a></li>
                        <li className={style.select} >
                            <a href="#">
                                Pages
                                <svg xmlns="http://www.w3.org/2000/svg" height="15px" viewBox="0 -960 960 960"
                                     width="24px" fill="#2a2a2a">
                                    <path d="M480-344 240-584l56-56 184 184 184-184 56 56-240 240Z"/>
                                </svg>
                            </a>
                            <ul className={style.menu}>
                                <li><NavLink to={'/about'}>About Us</NavLink></li>
                                <li><NavLink to={'/product'}>Products</NavLink></li>
                                <li><NavLink to={'/singleProduct'}>Single Product</NavLink></li>
                                <li><NavLink to={'/contact'}>Contact Us</NavLink></li>
                            </ul>
                        </li>
                        <li><a href="/#exploreSection">Explore</a></li>
                    </ul>
                </nav>
            </div>
        </header>
    );
}

export default Header;