import './App.css';
import HomePageLayout from "./component/homePage/homePageLayout/homePageLayout";
import RouterLayout from "./component/routerLayout/routerLayout";
import AboutPageLayout from "./component/aboutPage/aboutPageLayout/aboutPageLayout";
import {createBrowserRouter, RouterProvider} from "react-router-dom";
import ProductPageLayout from "./component/productPage/productPageLayout/productPageLayout";
import SingleProductPageLayout from "./component/singleProductPage/singleProductPageLayout/singleProductPageLayout";
import ContactPageLayout from "./component/contactPage/contactPageLayout/contactPageLayout";

function App() {
    const router = createBrowserRouter([{
        path: '/',
        element: <RouterLayout/>,
        children: [
            {
                index: true,
                path: '/',
                element: <HomePageLayout/>,
            },
            {
                path: '/about',
                element: <AboutPageLayout />,
            },
            {
                path: '/product',
                element: <ProductPageLayout />,
            },
            {
                path: '/singleProduct',
                element: <SingleProductPageLayout />
            },
            {
                path: '/contact',
                element: <ContactPageLayout />
            }
        ]
    }])
  return <RouterProvider router={router}/>
}

export default App;
