import React from 'react';
import style from './teamSection.module.css'
import firstImage from '../../../img/about/team-member-01.jpg'
import secondImage from '../../../img/about/team-member-02.jpg'
import thirdImage from '../../../img/about/team-member-03.jpg'
import FacebookRoundedIcon from '@mui/icons-material/FacebookRounded';
import TwitterIcon from '@mui/icons-material/Twitter';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import InstagramIcon from '@mui/icons-material/Instagram';
function TeamSection(props) {
    return (
        <section>
            <div className="title text-center">
                <h2>Our Amazing Team</h2>
                <p><i>Details to details is what makes Hexashop different from the other themes.</i></p>
            </div>
            <div className={style.container}>
                <div className={style.card}>
                    <div className={style.image}>
                        <img src={firstImage} alt=""/>
                        <div className={style.imageHover}>
                            <a href="#"><FacebookRoundedIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><TwitterIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><LinkedInIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><InstagramIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                        </div>
                    </div>
                    <div className={style.cardTitle}>
                        <h3>Ragnar Lodbrok</h3>
                        <p><i>Product Caretaker</i></p>
                    </div>
                </div>
                <div className={style.card}>
                    <div className={style.image}>
                        <img src={secondImage} alt=""/>
                        <div className={style.imageHover}>
                            <a href="#"><FacebookRoundedIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><TwitterIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><LinkedInIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><InstagramIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                        </div>
                    </div>
                    <div className={style.cardTitle}>
                        <h3>Ragnar Lodbrok</h3>
                        <p><i>Product Caretaker</i></p>
                    </div>
                </div>
                <div className={style.card}>
                    <div className={style.image}>
                        <img src={thirdImage} alt=""/>
                        <div className={style.imageHover}>
                            <a href="#"><FacebookRoundedIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><TwitterIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><LinkedInIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                            <a href="#"><InstagramIcon sx={{color: '#fff'}} fontSize={'medium'}/></a>
                        </div>
                    </div>
                    <div className={style.cardTitle}>
                        <h3>Ragnar Lodbrok</h3>
                        <p><i>Product Caretaker</i></p>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default TeamSection;